*,
*::before,
*::after {
	box-sizing: border-box;
}
/* * This project is just for acknowledging the essential use of `meta name="viewport"` tag in html. */

body {
	margin: 0;
	font-family: 'Roboto', sans-serif;
	font-size: 1.3rem;
}

:root {
	--margin-horizontal-navbar-items: 1rem;
}

img {
	max-width: 100%;
}

h1 {
	font-size: 3rem;
	margin-top: 0;
}

.section-title {
	color: #87629a;
}

.btn {
	display: inline-block;
	text-decoration: none;
	text-transform: uppercase;
	color: #23424a;
	font-weight: 900;
	background-color: #38cfd9;
	padding: 0.75em 2em;
	border-radius: 100px;
}

.btn:hover,
.btn:focus {
	opacity: 0.75;
}

.container {
	width: 80%;
	max-width: 1100px;
	margin: 0 auto;
}

.row {
	display: flex;
	justify-content: space-between;
}

header {
	background: #136c72;
	padding: 0.5em 0;
}

.nav {
	display: flex;
	justify-content: space-between;
	flex-grow: 1;
}

.logo {
	margin-right: 1em;
}

.nav__list {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	flex-direction: row;
	/* border: 2px solid red; */
}

.nav__list--primary {
	margin: 0 auto;
	/*         ^^ this just magically centers the nav to the center, yikes! */
}

.nav__item {
	font-size: 0.8rem;
	margin: 0.4rem 0;
}

.nav__item + .nav__item {
	margin-left: var(--margin-horizontal-navbar-items);
}

.nav__link {
	color: #fff;
	text-decoration: none;
	text-transform: uppercase;
}

.nav__link--button {
	background: #fff;
	color: #136c72;
	padding: 0.25em 1em;
	border-radius: 10em;
}

.nav__link:hover {
	opacity: 0.75;
}

.hero {
	padding: 100px 0;
	background-color: #23424a;
	color: #fff;
}

.hero__text {
	width: 62%;
}

.hero__img {
	width: 32%;
	align-self: flex-start;
}

.hero p {
	margin-bottom: 3em;
}

.main {
	margin-top: 3em;
}

.primary-content {
	width: 62%;
}

.sidebar {
	width: 32%;
	padding: 1em;
	text-align: center;
	color: #fff;
	background-color: #136c72;
}

@media (max-width: 900px) {
	.row {
		display: block;
	}

	.hero__text,
	.hero__img,
	.primary-content,
	.sidebar {
		width: 100%;
	}
	.hero__img {
		margin-top: 2em;
	}
}
